<template>
  <!-- 首页 -->
  <div class="home-box">
    <div class="item-icon-box" v-for="item in navList" :key="item.name">
      <p class="item-icon" @click="clickNavItem(item)" v-if="item.homeNav">
        <img :src="item.imgUrl" alt="" />
      </p>
      <p class="icon-text" v-if="item.homeNav">{{ item.name }}</p>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, onBeforeMount, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import { navList } from '@/views/home.js';
export default {
  name: '',
  setup(props, ctx) {
    const data = reactive({
      navList
    });
    const router = useRouter();
    // 点击导航每一项事件
    const clickNavItem = (nav) => {
      // 导航类型是page就打开新页面
      if (nav.type === 'page') {
        router.push({ path: nav.path });
      } else {
        // 触发父组件中打开组件模块
        ctx.emit('clickNavChange', nav);
      }
    };

    return {
      ...toRefs(data),
      clickNavItem
    };
  }
};
</script>
<style lang="less" scoped>
.home-box {
  width: 100%;
  height: 100%;
  padding: 20px 10px;
  .item-icon-box {
    float: left;
    width: 70px;
    margin: 0 10px;
    .item-icon {
      width: 100%;
      border-radius: 10px;
      cursor: pointer;
      transition: 0.3s;
      img {
        display: block;
        width: 100%;
        height: 70px;
      }
      &:hover {
        box-shadow: 0px 0px 10px #fff;
      }
    }
    .icon-text {
      margin-top: 5px;
      width: 100%;
      color: #fff;
      text-align: center;
      font-size: 14px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }
  }
}
</style>
